.hotels
{
	background-color: #FFFFFF;
	border-bottom: 1px solid rgba(207, 218, 230, 0.5);
}
.hotels__main
{
	margin-left: auto;
	margin-right: auto;
	padding-top: 42px;
	padding-bottom: 59px;
	overflow: hidden;
}
h2.hotels__title
{
	max-width: 804px;
	padding-left: 160px;
	padding-right: 160px;
	margin-bottom: 25px;
	color: #3A3A3A;
}
.hotels__descr
{
	max-width: 804px;
	padding-left: 160px;
	padding-right: 160px;
	margin: 0 auto;
	font-family: $regular;
	font-size: 16px;
	line-height: 20px;
	color: #303030;
	text-align: center;
	margin-bottom: 55px;
}
.hotels__map-view
{
	height: 340px;
}
@media (max-width: 1140px)
{
	h2.hotels__title
	{
		max-width: 540px;
		padding-left: 45px;
		padding-right: 45px;
	}
	.hotels__descr
	{
		max-width: 690px;
		padding-left: 45px;
		padding-right: 45px;
	}
}